.custom-date-time {
  height: 42px;

  input[type="time"],
  input[type="date"] {
    min-height: 100%;

    &::-webkit-calendar-picker-indicator {
      margin-left: 10px;
      background-position: left center;
      position: absolute;
      left: 0;
      background-size: auto;
    }

    &::-webkit-datetime-edit-fields-wrapper {
      position: absolute;
      left: 40px;
      top: 50%;
      transform: translateY(-50%);
    }

    &::-webkit-datetime-edit-fields-wrapper {
      visibility: hidden;
    }
  }

  input[type="time"] {
    &::-webkit-calendar-picker-indicator {
      background-image: url('../images/icons/time.svg')
    }
  }

  input[type="date"] {
    &::-webkit-calendar-picker-indicator {
      background-image: url('../images/icons/date.svg')
    }
  }

  input {
    border-top-left-radius: 4px !important;
    border-bottom-left-radius: 4px !important;
  }

  .rs-label {
    left: 40px;
    margin: 0;
    z-index: 9;
  }

  &.animate {
    .rs-label {
      left: 5px;
      padding: 0 4px;
    }

    input {
      &::-webkit-datetime-edit-fields-wrapper {
        visibility: visible;
      }
    }
  }
}

.rs-form {
  &.ra-form {
    position: relative;

    @media (max-width: 767px) {
      width: 100%;
      margin-top: 20px;
    }

    &.password-form {
      .rs-input {
        &.ra-input {
          padding-right: 50px;
        }
      }
    }

    &.animate {
      .rs-label {
        &.ra-label {
          top: 0%;
          left: 5px;
          font-size: rem(10);
          font-weight: 400;
        }
      }

      .rs-input {
        &.ra-input {
          border: 1px solid $primaryColor !important;
        }
      }
    }

    .rs-label {
      &.ra-label {
        left: 15px;
        top: 50%;
        font-size: rem(14);
        font-weight: 400;
        transform: translateY(-50%);
        counter-reset: $grayLightTextColor;
        transition: 0.2s ease-in-out;
        z-index: 10;
      }
    }

    .rs-input {
      &.ra-input {
        border-radius: 4px;
        width: 100%;
        transition: .1s ease;
        min-height: 40px;
        font-size: rem(14);
        font-weight: 500;
        color: $black;
        transition: 0.2s ease-in-out;

        &.ra-input-border-transparent {
          border: 1px solid transparent !important;

          &:focus {
            border: 1px solid $darkPrimaryColor !important;
          }
        }

        &.ra-input-border-primary {
          border: 1px solid $grayBorderColor !important;

          &:focus {
            border: 1px solid $darkPrimaryColor !important;
          }
        }

        &.ra-input-primary-bg {
          background-color: $primaryBg;
        }

        &.ra-input-dark-bg {
          background-color: $dataTableSearchBgColor;
        }

        &::placeholder {
          font-size: 14px;
          color: $grayLightTextColor;
        }

        @media (max-width: 992px) {
          min-height: 30px;
        }
      }
    }
  }
}

