/***********************************
// Client Dashboard Dropdown
**************************************/
.dropdown {
  &.dropdwon-3 {
    button {
      height: 52px;
      background: $white;
      padding: 8px 12px;
      border-radius: 3px;
      color: $primaryColor;
      border: 1px solid $purpleLight3;
      width: 100%;
      position: relative;
      transition: .2s;
    }

    .dropdown-menu {
      top: 12% !important;
      border: none;
      padding: 0px;

      ul {
        li {
          padding-left: 10px;
          margin-bottom: 0px;

          .checkbox-wrapper {
            font-size: 14px;
            font-weight: 500;
            display: flex;
            align-items: center;
            color: $primaryColor;
            padding: 10px 10px;
            margin-bottom: 0px;
            width: 100%;
          }

          &:hover {
            background-color: $purpleLight5;
          }
        }
      }
    }

    .disabled {
      .label-text {
        color: rgba($purpleLight, 0.6);
      }

      .disable-title {
        color: rgba($primaryColor, 0.6);
      }

      .disable-img {
        opacity: .4;
      }

      .dropdown-icon {
        position: absolute;
        left: 94%;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    .dropdown-icon {
      display: flex;
      position: absolute;
    }

    .dropdown-icon {
      &.up {
        transform: rotate(180deg) translateY(-50%);
        top: 21%;
        left: 94%;
        display: none;
      }

      &.down {
        top: 53%;
        left: 94%;
        transform: translateY(-50%);
      }
    }

    button:focus {
      border: 1px solid $greenDark;
    }

    button.disabled:focus {
      border: 1px solid $purpleLight3;
    }

    button:focus .label-text {
      color: $greenDark;
    }

    button:focus .dropdown-icon.down {
      display: none;
    }

    button:focus .dropdown-icon.up {
      display: block;
    }

    button.disabled:focus .label-text {
      color: rgba($purpleLight, 0.6);
    }

    button:focus .blur {
      color: rgba($primaryColor, 0.6);
      transition: .2s;
    }

    .dropdown-menu.show~button {
      border: 1px solid $greenDark;
    }

    .dropdown-menu.show~button.disabled {
      border: 1px solid $purpleLight3;
    }

    .dropdown-menu.show~button .label-text {
      color: $greenDark;
    }

    .dropdown-menu.show~button.disabled .label-text {
      color: rgba($purpleLight, 0.6);
    }

    .dropdown-menu.show~button:focus .blur {
      color: rgba($primaryColor, 0.6);
      transition: .2s;
    }
  }
}

.dropdown {
  &.dropdwon-3 {
    &.dropdwon-4 {

      .filter-btn {
        height: 36px;
        background: $purpleLight6;
        padding: 8px 12px;
        border-radius: 4px;
        color: $primaryColor;
        border: 1px solid transparent;

        &:focus {
          border: 1px solid transparent;
        }
      }

      .dropdown-menu.show~.filter-btn {
        border: 1px solid transparent;
        background: rgba($purpleLight6, .8);
      }

      .dropdown-icon.down.down-2 {
        left: 88%;
      }

      .dropdown-icon.up.up-2 {
        left: 88%;
        top: 10%;
      }

      .dropdown-menu {
        top: -25% !important;
        box-shadow: 0px 4px 8px rgba(36, 48, 97, 0.15);
        border: 1px solid $purpleLight3;
        border-radius: 4px;

        ul {
          p {
            padding: 10px;
          }
        }

        .disabled {
          pointer-events: none;
          opacity: .6;
        }
      }
    }
  }
}

.dropdown-icon {
  &.up-3 {
    position: absolute;
    transform: rotate(180deg) translateY(-50%);
    top: -3%;
    left: 84%;
    display: none;
  }

  &.down-3 {
    position: absolute;
    top: 43%;
    left: 84%;
    transform: translateY(-50%);
  }
}

button:focus .dropdown-icon.down-3 {
  display: none;
}

button:focus .dropdown-icon.up-3 {
  display: block;
}

