.multi-date-range {
  position: relative;

  .date-range {
    width: 98%;
    height: 52px;
    background: $white;
    padding: 8px 12px;
    border-radius: 3px;
    color: $primaryColor;
    border: 1px solid $purpleLight3;
    font-size: rem(14) !important;
    font-weight: 500;
    transition: .2s;
  }

  .daterange-label-text {
    position: absolute;
    top: -11px;
    left: 11px;
    z-index: 1;
    background: $white;
    color: $purpleLight;
    padding: 0px 5px;
    font-size: rem(12);
