  }
}

.multi-date-range {
  &.focus {
    .date-range {
      color: rgba($purpleLight, 0.6);
      border: 1px solid $greenDark;
    }

    .daterange-label-text {
      color: $greenDark;
    }

    .label-img {
      .daterange-svg-icon [fill] {
