  input~.checkmark.checkmark-green,
  .checkbox-green {
    background-color: $purpleLight5;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  input:checked~.checkmark.checkmark-green {
    background-color: $greenDark;
    border-color: transparent;
  }

  input:checked~span {
    font-weight: 500 !important;
  }
}

.dropdown-search {
  position: relative;
  border-bottom: 1px solid $purpleLight3;
}

.dropdown-search {
  label {
    position: absolute;
    right: 6px;
    top: 44%;
    transform: translateY(-50%);
    opacity: .6;
  }

  input {
    border: 1px solid transparent;
    width: 100%;
    padding: 5px 45px 5px 10px;
    position: relative;
    transition: .2s;
    border-radius: 4px;
  }

  &.searchInDropdown {
    input {
  
      &:active,
      &:focus,
      &:visited {
        border: 1px solid $greenDark;
      }
  
    }
  }
}

.animate-search {
  position: relative;

  .hidden-search-icon {
    position: relative;
    z-index: 1;
  }

  .hidden-search {
    position: absolute;
    width: 0%;
    transition: .3s ease-in-out;
    top: -4px;
    left: 0;
    border-radius: 4px;
    border-color: $greenDark;
    opacity: 0;
    visibility: hidden;

    &.show {
      border: 1px solid $greenDark !important;
      width: 100%;
      z-index: 2;
      opacity: 1;
      visibility: visible;
    }
  }
}


/***********************************
// Custom Daterange Picker
**************************************/
.daterangepicker .calendar-table .prev span {
  background-image: url("../images/icons/arrow-short-left.svg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 16px;
  transform: rotate(0deg);
}

.daterangepicker .calendar-table .next span {
  background-image: url("../images/icons/arrow-short-left.svg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 16px;
  transform: rotate(180deg);
}

.daterangepicker .calendar-table .next span,
.daterangepicker .calendar-table .prev span {
  color: $white;
  border: unset;
  padding: 3px;
  height: 12px;
  width: 18px;
}

.prev.available,
.next.available {
  display: flex;
  justify-content: center;
  align-items: center;
}

.daterangepicker td.available,
.daterangepicker th.available {
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.daterangepicker td.available:hover,
.daterangepicker th.available:hover {
  background-color: rgba($orangDark, .10);
  color: $purpleLight;
}

.month {
  color: $purple;
  font-size: rem(20);
  font-weight: 700;
}

.daterangepicker .calendar-table th {
  color: $purple;
  font-size: rem(14);
  font-weight: 700;
}

.daterangepicker .calendar-table td {
  color: $purpleLight;
  font-size: rem(14);
  font-weight: 500;
}

.daterangepicker .calendar-table tr {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
}

.daterangepicker td.active,
.daterangepicker td.active:hover {
  background-color: $orangDark !important;
  border-color: transparent;
  color: $white !important;
  border-radius: 50% !important;
  height: 32px;
  width: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.daterangepicker td.start-date {
  border-radius: 0px;
  font-weight: 600;
}

.daterangepicker td.in-range {
  background-color: $orangLight;
  border-color: transparent;
  color: $orangDark;
  border-radius: 50%;
  font-weight: 600;
}

.progress-bar {
  &.green {
    background-color: $greenDark;
  }
}